import { Card, CardHeader, Typography } from "@material-ui/core";
import * as React from "react";
import { Link } from "react-router-dom";
import { HomeProps, HomeState } from "src/model/HomeModel";
import LinkBean from "src/model/LinkBean";
import "../../bootstrap.min.css";
import LoadingComponent from "../components/LoadingComponent";
import "./Home.css";

class Home extends LoadingComponent<HomeProps, HomeState> {
	private mounted = false;

	constructor(p: HomeProps) {
		super(p);
		this.state = {
			loading: true
		};
		this.setState = this.setState.bind(this);
	}

	public onLoad() {
		this.mounted = true;
		const callback = (data: LinkBean[]) => {
			if (this.mounted) {
				this.setState({ ...this.state, links: data });
			}
			this.log("LOAD");
		};
		this.fetchData(callback.bind(this));
	}

	public renderPostLoad() {
		const { links } = this.state;

		if (!links) {
			return this.loadingElement;
		}

		return (
			<div
				className="App transparent"
				style={{
					borderRadius: 15,
					margin: 20
				}}
			>
				<Typography component="h2" variant="h1" gutterBottom={true}>
					Welcome!
				</Typography>
				<hr />

				<Typography component="h2" variant="h3" gutterBottom={true}>
					Here are some of the projects available
				</Typography>
				{links!.map(this.generateLink)}
			</div>
		);
	}

	public componentWillUnmount() {
		this.mounted = false;
	}

	protected getLoadTime(): number {
		return 1000;
	}

	private generateLink(link: LinkBean) {
		return (
			<div
				key={link.name}
				style={{
					textAlign: "center",
					display: "inline-block",
					float: "left",
					margin: 10
				}}
			>
				<Link to={link.url}>
					<Card
						className="card-link"
						style={{ padding: 15, maxWidth: 400 }}
					>
						<CardHeader
							title={link.name}
							subheader={link.subheader}
						/>
						<img
							style={{ height: 150 }}
							src={link.img}
							title={`${link.name} Image`}
						/>
					</Card>
				</Link>
			</div>
		);
	}

	private fetchData(callback: (data: LinkBean[]) => void): void {
		const log = this.log.bind(this);
		$.ajax({
			url: "/homeData.json",
			dataType: "json",
			cache: false,
			success: callback,
			error(xhr, status, err) {
				log(err);
				alert(err);
			}
		});
	}
}
export default Home;
